.signin-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background: linear-gradient(to bottom, #68B545 50%, #ffffff 50%);
}

.signin-form {
  width: 300px;
  padding: 20px;
  background-color: #ffffff;
  border-radius: 4px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
}

.signin-form h2 {
  text-align: center;
  margin-bottom: 20px;
}

.form-group {
  display: flex;
  align-items: center;
  margin-bottom: 15px;
}

.form-group .icon {
  width: 16px;
  height: 16px;
  margin-right: 10px;
  color: #ff7f50;
}

.form-group input {
  flex-grow: 1;
  padding: 10px;
  border: none;
  border-bottom: 1.42424px solid #e7e7e7;
  border-radius: 0;
  background-color: transparent;
}

button[type="submit"] {
  display: block;
  width: 100%;
  padding: 10px;
  background-color: #ff7f50;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  margin-top: 10px;
}

button[type="submit"]:hover {
  background-color: #ff6347;
}

p {
  text-align: center;
  margin-top: 10px;
}

a {
  color: #ff7f50;
}

.toggle-password-icon {
  position: absolute;
  right: 10px;
  top: 50%;
  transform: translateY(-50%);
  cursor: pointer;
  color: #68b545;
  font-size: 14px; /* Adjust the font size as needed */
}
